"use client"

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Progress } from '@/components/ui/progress'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'

interface ReportData {
  employee: {
    id: string
    name: string
    email: string
  }
  timeTracking: {
    today: number
    thisWeek: number
    thisMonth: number
    productivity: number
    idleTime: number
    breaks: number
  }
  applications: Array<{
    name: string
    timeSpent: number
    category: string
    productivity: number
  }>
  screenshots: {
    total: number
    flagged: number
    lastTaken: Date
  }
  activities: Array<{
    id: number
    activity: string
    timestamp: Date
    duration: number
    application: string
  }>
}

export default function ReportsPage() {
  const [reportData, setReportData] = useState<ReportData[]>([])
  const [selectedEmployee, setSelectedEmployee] = useState<string>('all')
  const [dateRange, setDateRange] = useState<string>('today')
  const [loading, setLoading] = useState(true)
  const [reportSummary, setReportSummary] = useState<any>(null)

  useEffect(() => {
    fetchReports()
  }, [selectedEmployee, dateRange])

  const fetchReports = async () => {
    setLoading(true)
    try {
      const params = new URLSearchParams({
        type: 'detailed',
        dateRange,
        ...(selectedEmployee !== 'all' && { employeeId: selectedEmployee })
      })
      
      const response = await fetch(`/api/reports?${params}`)
      const data = await response.json()
      
      setReportData(data.data)
      setReportSummary(data.summary)
    } catch (error) {
      console.error('Failed to fetch reports:', error)
    } finally {
      setLoading(false)
    }
  }

  const formatTime = (seconds: number) => {
    const hours = Math.floor(seconds / 3600)
    const minutes = Math.floor((seconds % 3600) / 60)
    return `${hours}h ${minutes}m`
  }

  const formatMinutes = (minutes: number) => {
    const hours = Math.floor(minutes / 60)
    const mins = minutes % 60
    return hours > 0 ? `${hours}h ${mins}m` : `${mins}m`
  }

  const getCategoryColor = (category: string) => {
    const colors: { [key: string]: string } = {
      'Productivity': 'bg-green-500',
      'Development': 'bg-blue-500',
      'Communication': 'bg-purple-500',
      'Design': 'bg-pink-500',
      'Browser': 'bg-orange-500',
      'Entertainment': 'bg-red-500',
      'Utility': 'bg-gray-500'
    }
    return colors[category] || 'bg-gray-500'
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 p-6">
        <div className="max-w-7xl mx-auto">
          <div className="text-center py-12">
            <div className="text-lg">Loading reports...</div>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="flex justify-between items-center mb-8">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Employee Reports</h1>
            <p className="text-gray-600">Comprehensive tracking and application usage reports</p>
          </div>
          <div className="flex gap-4">
            <Button 
              variant="outline" 
              onClick={() => window.open('/admin', '_blank')}
            >
              Back to Admin
            </Button>
            <Button onClick={() => window.print()}>
              Export Report
            </Button>
          </div>
        </div>

        {/* Filters */}
        <Card className="mb-6">
          <CardHeader>
            <CardTitle>Report Filters</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div>
                <Label htmlFor="employee-select">Employee</Label>
                <Select value={selectedEmployee} onValueChange={setSelectedEmployee}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select employee" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">All Employees</SelectItem>
                    {reportData.map((data) => (
                      <SelectItem key={data.employee.id} value={data.employee.id}>
                        {data.employee.name}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
              <div>
                <Label htmlFor="date-range">Date Range</Label>
                <Select value={dateRange} onValueChange={setDateRange}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select date range" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="today">Today</SelectItem>
                    <SelectItem value="week">This Week</SelectItem>
                    <SelectItem value="month">This Month</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div className="flex items-end">
                <Button onClick={fetchReports} className="w-full">
                  Generate Report
                </Button>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Summary Stats */}
        {reportSummary && (
          <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
            <Card>
              <CardHeader className="pb-2">
                <CardTitle className="text-sm font-medium">Total Employees</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{reportSummary.totalEmployees}</div>
                <p className="text-xs text-gray-600">Active: {reportSummary.activeEmployees}</p>
              </CardContent>
            </Card>
            <Card>
              <CardHeader className="pb-2">
                <CardTitle className="text-sm font-medium">Total Hours</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{formatTime(reportSummary.totalHoursToday)}</div>
                <p className="text-xs text-gray-600">Today combined</p>
              </CardContent>
            </Card>
            <Card>
              <CardHeader className="pb-2">
                <CardTitle className="text-sm font-medium">Avg Productivity</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold text-green-600">{reportSummary.averageProductivity}%</div>
                <Progress value={reportSummary.averageProductivity} className="mt-2" />
              </CardContent>
            </Card>
            <Card>
              <CardHeader className="pb-2">
                <CardTitle className="text-sm font-medium">Top Application</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="text-lg font-bold">{reportSummary.topApplications[0]?.name || 'N/A'}</div>
                <p className="text-xs text-gray-600">
                  {reportSummary.topApplications[0] ? formatMinutes(reportSummary.topApplications[0].time) : '0m'}
                </p>
              </CardContent>
            </Card>
          </div>
        )}

        <Tabs defaultValue="overview" className="space-y-6">
          <TabsList className="grid w-full grid-cols-4">
            <TabsTrigger value="overview">Overview</TabsTrigger>
            <TabsTrigger value="applications">Applications</TabsTrigger>
            <TabsTrigger value="activities">Activities</TabsTrigger>
            <TabsTrigger value="screenshots">Screenshots</TabsTrigger>
          </TabsList>

          <TabsContent value="overview">
            <Card>
              <CardHeader>
                <CardTitle>Employee Time Tracking Overview</CardTitle>
              </CardHeader>
              <CardContent>
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Employee</TableHead>
                      <TableHead>Today</TableHead>
                      <TableHead>This Week</TableHead>
                      <TableHead>This Month</TableHead>
                      <TableHead>Productivity</TableHead>
                      <TableHead>Idle Time</TableHead>
                      <TableHead>Breaks</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {reportData.map((data) => (
                      <TableRow key={data.employee.id}>
                        <TableCell>
                          <div>
                            <div className="font-medium">{data.employee.name}</div>
                            <div className="text-sm text-gray-600">{data.employee.email}</div>
                          </div>
                        </TableCell>
                        <TableCell className="font-medium">
                          {formatTime(data.timeTracking.today)}
                        </TableCell>
                        <TableCell>
                          {formatTime(data.timeTracking.thisWeek)}
                        </TableCell>
                        <TableCell>
                          {formatTime(data.timeTracking.thisMonth)}
                        </TableCell>
                        <TableCell>
                          <div className="flex items-center gap-2">
                            <span className="text-sm font-medium">{data.timeTracking.productivity}%</span>
                            <Progress value={data.timeTracking.productivity} className="w-16" />
                          </div>
                        </TableCell>
                        <TableCell className="text-orange-600">
                          {formatMinutes(data.timeTracking.idleTime)}
                        </TableCell>
                        <TableCell>{data.timeTracking.breaks}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="applications">
            <div className="space-y-6">
              {reportData.map((data) => (
                <Card key={data.employee.id}>
                  <CardHeader>
                    <CardTitle>{data.employee.name} - Application Usage</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <Table>
                      <TableHeader>
                        <TableRow>
                          <TableHead>Application</TableHead>
                          <TableHead>Category</TableHead>
                          <TableHead>Time Spent</TableHead>
                          <TableHead>Productivity Score</TableHead>
                        </TableRow>
                      </TableHeader>
                      <TableBody>
                        {data.applications.map((app, index) => (
                          <TableRow key={index}>
                            <TableCell className="font-medium">{app.name}</TableCell>
                            <TableCell>
                              <Badge className={`${getCategoryColor(app.category)} text-white`}>
                                {app.category}
                              </Badge>
                            </TableCell>
                            <TableCell>{formatMinutes(app.timeSpent)}</TableCell>
                            <TableCell>
                              <div className="flex items-center gap-2">
                                <span className="text-sm">{app.productivity}%</span>
                                <Progress value={app.productivity} className="w-16" />
                              </div>
                            </TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </CardContent>
                </Card>
              ))}
            </div>
          </TabsContent>

          <TabsContent value="activities">
            <div className="space-y-6">
              {reportData.map((data) => (
                <Card key={data.employee.id}>
                  <CardHeader>
                    <CardTitle>{data.employee.name} - Recent Activities</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <Table>
                      <TableHeader>
                        <TableRow>
                          <TableHead>Activity</TableHead>
                          <TableHead>Time</TableHead>
                          <TableHead>Duration</TableHead>
                          <TableHead>Application</TableHead>
                        </TableRow>
                      </TableHeader>
                      <TableBody>
                        {data.activities.slice(0, 10).map((activity) => (
                          <TableRow key={activity.id}>
                            <TableCell className="font-medium">{activity.activity}</TableCell>
                            <TableCell className="text-sm text-gray-600">
                              {new Date(activity.timestamp).toLocaleString()}
                            </TableCell>
                            <TableCell>{formatMinutes(activity.duration)}</TableCell>
                            <TableCell>
                              <Badge variant="outline">{activity.application}</Badge>
                            </TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </CardContent>
                </Card>
              ))}
            </div>
          </TabsContent>

          <TabsContent value="screenshots">
            <div className="space-y-6">
              {reportData.map((data) => (
                <Card key={data.employee.id}>
                  <CardHeader>
                    <CardTitle>{data.employee.name} - Screenshot Summary</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                      <div className="text-center p-4 bg-blue-50 rounded-lg">
                        <div className="text-2xl font-bold text-blue-600">{data.screenshots.total}</div>
                        <div className="text-sm text-gray-600">Total Screenshots</div>
                      </div>
                      <div className="text-center p-4 bg-red-50 rounded-lg">
                        <div className="text-2xl font-bold text-red-600">{data.screenshots.flagged}</div>
                        <div className="text-sm text-gray-600">Flagged Screenshots</div>
                      </div>
                      <div className="text-center p-4 bg-green-50 rounded-lg">
                        <div className="text-sm font-bold text-green-600">
                          {new Date(data.screenshots.lastTaken).toLocaleString()}
                        </div>
                        <div className="text-sm text-gray-600">Last Screenshot</div>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  )
}
